/* heroimage */
.homehover {
    background-image: url(../assets/HomeImgs/1.png);
    background-size: cover;
    /* background-attachment: fixed;
    background-size: 115%; */
    padding: 15rem 2rem;
    margin-bottom: 20px;
    filter: grayscale(1);
    z-index: 0;
    
}

.homehover:hover {
    /* background-image: url(../assets/HomeImgs/1.png); */
    background-size: cover;
    /* background-attachment: fixed;
    background-size: 115%; */
    filter: grayscale(0);
}

.homehover h1 {
    font-family: 'Kaushan Script';
    color: #F8F9FA;
    text-align: center;
    font-size: 75px;
    text-shadow: 2px 2px 3px #495057;
    /*  495057  6C757D ADB5BD CED4DA */
}

.homeheader h1 {
    font-family: 'Kaushan Script';
    text-align: center;
    padding-top: 50px;
}
.homeheader h2 {
    padding-top: 25px;
    text-align: center;
    padding-bottom: 15px;
}

.crown {
    font-size: 0.75em;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
}

.blurb {
    margin-top: 25px;
    text-align: center;
    font-size: 22px;
    margin-bottom: 50px;
}
.gridElements{
    height: 400vh;
    position: relative;
    width: 32%;
    height: 32%;
    float: right;
    justify-content: space-evenly; 
    object-fit: contain;
    overflow: hidden;
    color: white;
    padding: 5px 5px 5px 5px;
  }

  .gridElements img{
    width: 100%;
    height: 100%;
  }

  .imgWithText{
    position: relative;
    text-align: left;
    color: black;
    text-align: left;
    filter: grayscale(1);
  }

  .imgWithText:hover {
    filter: grayscale(0);
  }

  .bottom-left {
    font-family: 'Oswald';
    color: #F8F9FA;
    padding: 5px;
    bottom: 8px;
    left: 16px;
    z-index: 100;
    position: absolute;
    font-size: 24px;
    font-weight: bold;
    text-shadow: 5px 5px 8px #212529;
  } 


  @media (max-width: 800px) {
    .jumbotron h1 {
      font-size: 2em;
    }
    .homehover {
      background-image: url(../assets/HomeImgs/7.png);
      background-size: cover;
      max-height: 100px;
      max-width: 100%;
      /* filter: none; */
    }

    .homeheader h1{
      font-size: 18px;
    }

    .homeheader h2{
      font-size: 18px;
    }

    .bottom-left {
      font-size: 14px;
      padding: 2px;
    bottom: 0px;
    margin-bottom: 2px;
    left: 6px;
    font-weight: bolder;
    }
  }
