@media (max-width: 800px){
  .sidenav {
      padding-top: 15px;
      width: 100px;
  }
  .sidenav h2 {
    font-size: 16px;
  }
  .sidenav a {
      font-size: 14px;
      text-align: center;
  }
  .sidenav ul {
    padding-left: 15px;
  }
  body {
    margin-left: 95px;
    margin-right: 0px !important;
  }
  .dropdown-content .dropdowna{
    font-size: 10px;
  }
}

.dropbtn {
  display: inline-block;
  color: #F8F9FA;
  text-align: center;
  padding: 0px 16px;
  text-decoration: none;
}

li.dropdown {
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  /* background-color: #f9f9f9; */
  min-width: 160px;
  /* box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2); */
  z-index: 1;
}

.dropdown-content a {
  color: #F8F9FA;
  padding: 5px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
  font-size: 17px;
}

/* .dropdown-content a:hover {background-color: #f1f1f1} */

.dropdown:hover .dropdown-content {
  display: block;
}