 /* heroimage */
.abouthover {
    background-image: url(../assets/AboutImgs/6.png);
    background-size: cover;
    padding: 15rem 2rem;
    margin-bottom: 20px;
    /* filter: grayscale(1); */
    z-index: 0;
    /* background-attachment: fixed; */
}

.aboutsecond {
    font-family: Oswald;
    font-weight: bolder;
}

.item {
    width: auto;
    height: 200px;
    margin: 50px auto;
    padding-top: 75px;
    background: #E9ECEF;
    text-align: center;
    color: #495057;
    font-size: 1.5em;
  }

.abouthover:hover {
    background-image: url(../assets/AboutImgs/5.png);
    background-size: cover;
    /* filter: grayscale(0); */
    /* background-attachment: fixed; */
}

.abouthover h1 {
    font-family: 'Kaushan Script';
    color: #F8F9FA;
    text-align: center;
    font-size: 60px;
    text-shadow: 2px 2px 3px #495057;
    /*  495057  6C757D ADB5BD CED4DA */
}

.aboutheader h1 {
    font-family: 'Kaushan Script';
    text-align: center;
    padding-top: 50px;
}
.aboutheader h2 {
    padding-top: 25px;
    text-align: center;
    padding-bottom: 50px;
}

.crown {
    font-size: 0.75em;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
}

.aboutblurb {
    margin-top: 25px;
    text-align: justify;
    font-size: 22px;
    margin-left: 15px;
    margin-right: 15px;
}

@media (max-width: 800px) {
    .jumbotron h1 {
      font-size: 2em;
    }
    .abouthover {
      background-size: cover;
      max-height: 100px;
      max-width: 100%;
      filter: none;
    }

    .aboutheader h1{
      font-size: 20px;
    }

    .aboutheader h2{
      font-size: 20px;
    }
    .item {
        font-size: .75em;
    }
  }
